<template>
  <div>
    <div v-if="showYoutubePlayer()" class="smartideo">
      <div class="player">
        <iframe
          width="560"
          height="315"
          :src="youtubeEmbedUrl"
          frameborder="0"
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        ></iframe>
      </div>
    </div>
    <div
      v-else-if="youtubeFormats.length > 0"
      v-for="(format, index) in youtubeFormats"
      :key="format.videoUrl + index"
      style="width: 640px"
      class="wp-video"
    >
      <video
        class="wp-video-shortcode"
        width="640"
        height="360"
        preload="metadata"
        controls="controls"
        :style="
          youtubeVideoUrl === format.videoUrl
            ? 'display: block;'
            : 'display: none;'
        "
      >
        <source type="video/mp4" :src="format.videoUrl" />
        <a :href="format.videoUrl">
          {{ videoTitle }}
        </a>
      </video>
    </div>
    <div v-else style="width: 640px" class="wp-video">
      <video
        class="wp-video-shortcode"
        id="video-406232-1"
        width="640"
        height="360"
        preload="metadata"
        controls="controls"
      >
        <source type="video/mp4" :src="videoUrl" />
        <a :href="videoUrl">
          {{ videoTitle }}
        </a>
      </video>
    </div>

    <div
      v-if="useProxyForYoutubeVideo !== undefined"
      style="text-align: center"
    >
      <b-form-checkbox
        v-if="youtubeFormats.length"
        v-model="useProxyForYoutubeVideo"
        @change="useProxyForYoutubeVideoValueChanged"
        button
      >
        Youtube
      </b-form-checkbox>
      <b-form-select
        v-model="youtubeVideoUrl"
        :options="youtubeFormats"
      ></b-form-select>
      <b-button variant="success" @click.prevent="downloadYoutubeVideo()">
         MP4
      </b-button>
    </div>

    <div v-if="showLoadYoutube" style="text-align: center">
      <b-input-group class="mt-3">
        <b-form-input
          v-model="youtubeWatchUrl"
          placeholder="YouTube"
          trim
        ></b-form-input>
        <b-input-group-append v-if="!loadingYoutubeInfo">
          <b-button
            type="submit"
            variant="success"
            v-on:click="loadYoutubeInfo()"
          >
            <b-icon icon="check-circle"></b-icon>
          </b-button>
          <b-button
            v-if="history.length"
            variant="info"
            v-on:click="showHistory = !showHistory"
          >
            <b-icon v-if="showHistory" icon="chevron-double-up"></b-icon>
            <b-icon v-else icon="chevron-double-down"></b-icon>
          </b-button>
        </b-input-group-append>
      </b-input-group>

      <div v-if="showHistory">
        <b-input-group
          v-for="(item, index) in history"
          :key="item.id + index"
          class="mt-3"
        >
          <b-form-input readonly :value="item.title"></b-form-input>
          <b-input-group-append>
            <b-button variant="warning" v-on:click="removeFromHistory(item)">
              <b-icon icon="x-circle"></b-icon>
            </b-button>
          </b-input-group-append>
          <b-button variant="info" v-on:click="castVideo(item)">
            <b-icon icon="cast"></b-icon>
          </b-button>
        </b-input-group>
      </div>
    </div>

    <div v-if="statusMessage" style="text-align: center">
      <h6>{{ statusMessage }}</h6>
    </div>

    <p style="text-align: center">
      <strong>{{ videoTitle }}</strong>
    </p>
    <div v-html="videoDescription"></div>
  </div>
</template>

<script>
import Parse from "parse";
import { mapGetters, mapActions } from "vuex";
import { downloadFile } from "@/common/functions";

export default {
  name: "Video",
  data: function() {
    return {
      material: undefined,
      videoUrl: undefined,
      videoTitle: undefined,
      videoDescription: undefined,
      youtubeId: undefined,
      youtubeVideoUrl: undefined,
      youtubeWatchUrl: undefined,
      youtubeEmbedUrl: undefined,
      useProxyForYoutubeVideo: undefined,
      youtubeFormats: [],
      showLoadYoutube: false,
      loadingYoutubeInfo: false,
      statusMessage: undefined,
      showHistory: false
    };
  },
  async beforeRouteEnter(to, from, next) {
    const videoId = to.params.videoId;
    console.log(`videoId: ${videoId}`);
    let material = undefined;

    if (videoId) {
      try {
        const query = new Parse.Query("Video");
        const video = await query.get(videoId);
        material = video.get("content");
        console.log(`video url: ${material.url}`);
      } catch (error) {
        console.error("error loading video", error);
      }
    }

    next(vm => {
      vm.init(material);
    });
  },
  computed: {
    ...mapGetters("video", ["useProxyForYoutubeVideoPref", "history"])
  },
  methods: {
    ...mapActions("video", [
      "updateUseProxyForYoutubeVideoPref",
      "addToHistory",
      "removeFromHistory"
    ]),
    showYoutubePlayer() {
      return this.youtubeEmbedUrl && !this.useProxyForYoutubeVideo;
    },
    getProxiedVideoUrl(url, videoStreamingProxy) {
      const videoProxyBaseUrl =
        videoStreamingProxy || process.env.VUE_APP_PARENT_URL;
      let proxiedVideoUrl;
      if (!(url.includes("://") || url.indexOf("//") === 0)) {
        proxiedVideoUrl = videoProxyBaseUrl + url.replace("..", "");
      } else {
        proxiedVideoUrl = `${videoProxyBaseUrl}/proxy/${encodeURIComponent(
          url
        )}.mp4`;
      }
      console.log(`proxied videoUrl: ${proxiedVideoUrl}`);
      return proxiedVideoUrl;
    },
    init(material) {
      console.log(`init - material: ${material}`);
      let videoUrl;
      if (material) {
        videoUrl = material.url;
        this.setVideoDetails(material.name, material.description);
      } else {
        this.youtubeId = this.$route.query.v;
        if (!this.youtubeId) {
          videoUrl = this.$route.query.u;
        }
        this.setVideoDetails(this.$route.query.t);
      }

      console.log(`videoUrl: ${videoUrl}`);
      if (this.youtubeId || (videoUrl && videoUrl.includes("youtu"))) {
        this.videoUrl = videoUrl;
        this.loadYoutubeInfo();
      } else if (videoUrl) {
        this.videoUrl = this.getProxiedVideoUrl(videoUrl);
      } else {
        this.showLoadYoutube = true;
      }
    },
    setVideoDetails(
      title,
      description = undefined,
      downloadUrl = undefined,
      formats = undefined,
      videoStreamingProxy = undefined
    ) {
      if (title) {
        this.videoTitle = title;
        document.getElementById("videoTitle").innerHTML = this.videoTitle;
        document.title = this.videoTitle;
        console.log(`videoTitle: ${this.videoTitle}`);
      }
      if (description) {
        this.videoDescription = description.replace(/(.*)/g, "<p>$1</p>");
      }
      if (downloadUrl) {
        this.useProxyForYoutubeVideo =
          this.useProxyForYoutubeVideoPref || false;
        console.log(
          `useProxyForYoutubeVideoPref: ${this.useProxyForYoutubeVideoPref} useProxyForYoutubeVideo: ${this.useProxyForYoutubeVideo}`
        );
        this.youtubeVideoUrl = this.getProxiedVideoUrl(
          downloadUrl,
          videoStreamingProxy
        );
        this.addToHistory({ id: this.youtubeId, title: this.videoTitle });
      }
      if (formats) {
        this.youtubeFormats = formats.map(e => {
          const videoUrl = this.getProxiedVideoUrl(e.url, videoStreamingProxy);
          const array = e.format.split("-");
          const text = array[array.length - 1].trim();
          return {
            videoUrl,
            text,
            value: videoUrl
          };
        });
      } else {
        this.youtubeFormats = [];
      }
    },
    useProxyForYoutubeVideoValueChanged(checked) {
      console.log(`new useProxyForYoutubeVideo value: ${checked}`);
      this.updateUseProxyForYoutubeVideoPref(checked);
    },
    castVideo(item) {
      this.youtubeWatchUrl = `https://www.youtube.com/watch?v=${item.id}`;
    },
    loadYoutubeInfo() {
      if (!this.youtubeId && (this.youtubeWatchUrl || this.videoUrl)) {
        const youtubeURL = new URL(this.youtubeWatchUrl || this.videoUrl);

        if (youtubeURL.pathname === "/watch") {
          const parts = youtubeURL.search.split(/[?=&]/);
          for (let i = 0; i < parts.length; i++) {
            if (parts[i] === "v") {
              this.youtubeId = parts[i + 1];
              break;
            }
          }
        } else {
          this.youtubeId = youtubeURL.pathname.substring(1);
        }
      }

      if (this.loadingYoutubeInfo || !this.youtubeId) {
        return;
      }

      console.log(`loading youtube info: ${this.youtubeId}`);

      this.loadingYoutubeInfo = true;
      this.showHistory = false;
      this.statusMessage = "...";
      this.youtubeEmbedUrl = `https://www.youtube.com/embed/${this.youtubeId}`;
      const thisComponent = this;

      Parse.Cloud.run("video:loadYoutubeInfo", { youtubeId: this.youtubeId })
        .then(result => {
          console.log(
            `video:loadYoutubeInfo - result: ${JSON.stringify(result, null, 4)}`
          );

          thisComponent.setVideoDetails(
            result.title,
            result.description,
            result.downloadUrl,
            result.formats,
            result.videoStreamingProxy
          );
          thisComponent.loadingYoutubeInfo = false;
          thisComponent.statusMessage = thisComponent.youtubeFormats.length
            ? undefined
            : "";
        })
        .catch(e => {
          console.log(`error in video:loadYoutubeInfo: ${e}`);
          thisComponent.loadingYoutubeInfo = false;
          thisComponent.statusMessage = e;
        });
    },
    downloadYoutubeVideo() {
      let format;
      for (let i = 0; i < this.youtubeFormats.length; i++) {
        format = this.youtubeFormats[i];
        if (this.youtubeVideoUrl === format.videoUrl) {
          break;
        }
      }

      const downloadFieName = `${this.videoTitle}-${format.text}.mp4`;
      this.statusMessage = ` ${downloadFieName}`;

      const thisComponent = this;

      downloadFile(this.youtubeVideoUrl, downloadFieName)
        .then(() => {
          thisComponent.statusMessage = undefined;
        })
        .catch(error => {
          thisComponent.statusMessage = ` ${error}`;
        });
    }
  }
};
</script>
